<template>
	<div class="personal-content-body">
		<div class="table_content">
			<div class="table_content_title flex_start">
				<span>系统管理</span>
			</div>
			<div class="table_content_body">
				<div class="table_content_body_left">
					<div v-for="item in leftNav" class="left_nav cursor">
						<router-link :to="'/home/systemManageHome/'+item.component">
							<i class="icon iconfont" :class="'icon-'+item.icon"></i>
							<span>{{ item.text }}</span>
						</router-link>
					</div>
				</div>
				<div class="table_content_body_right">
					<router-view></router-view>
				</div>
				<div class="clear"> </div>
			</div>
		</div>

	</div>
</template>

<script>
	import { getRouterListApi } from '../../config/api'
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	
	export default {
		name: 'systemManageHome',
		
		data() {
			return {
				componentName: 'employeesManage',
				leftNav: [
					{
						component: "employeesManage",
						icon: 'neiburenyuanguanli',
						text: '内部人员管理'
					},
					{
						component: "transferManager",
						icon: 'guanliyuanfenpei',
						text: '管理人员分配'
					},
					{
						component: "organizationStructure",
						icon: 'zuzhijiagou',
						text: '组织架构'
					},
//					{
//						component: "approvalConfigureHome",
//						icon: 'jijinquanxian',
//						text: '审批配置'
//					},
					{
						component: "vacationManage",
						icon: 'jiaqipeizhi',
						text: '假期配置'
					},
//					{
//						component: "weeklySchedule",
//						icon: 'zhougongzuoanpai',
//						text: '周工作安排'
//					},
					{
						component: "organizationInfoPage",
						icon: 'jigouxinxi',
						text: '机构信息'
					},
//					{
//						component: "publishSystemNews",
//						icon: 'fabugonggao',
//						text: '发布公告'
//					},
					{
						component: "attendanceDetails",
						icon: 'chuqinjilu',
						text: '出勤记录'
					},
//					{
//						component: "goOutCard",
//						icon: 'dakajilu',
//						text: '打卡记录'
//					},
					
				]
			};
		},
		created() {
			this.getRouterList()
		},
		methods: {
			// 获取导航路由数据
			getRouterList(){
				getRouterListApi({
					specMenuId: 22
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.leftNav = res.data.payload;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
		},
	}
</script>

<style scoped="scoped">
	.table_group_title_delete {
		margin-right: 20px;
		font-size: 14px;
		color: #6292F4;
	}
	.table_content_title span{
		font-size: 18px;
	}

</style>


